.content {
  background-color: rgb(219, 227, 253);
  padding: 0 7vw;
}

.forms {
  width: 600px;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 10px;

}

.form-details {
  text-align: center;
  margin-bottom: 10px;
}

.form-details h2 {
  font-size: 50px;
  color: #333;
  font-weight: bold;
  padding-bottom: 15px;

}

.form-details p {

  text-align: center;
  font-size: 15px;
  font-weight: bold;
  padding-top: 40px;
  padding-bottom: 10px;
  padding-left: 10px;


}

.form-content {
  background-color: #f9f9f9;
  padding: 5px;
  border-radius: 10px;
}

.inputs {
  text-align: center;
  padding-bottom: 3px;

}

.inputs input {
  border-radius: 20px;
  width: 500px;
  padding: 3px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.inputs label {
  font-size: 20px;
  display: block;

}

.form-content button {
  width: 100%;
  padding: 10px;
  background-color: rgb(123, 195, 243);
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bolder;
}

.form-content button:hover {
  background-color: #00060a;
}